.cell {
    padding-top: 33%;
    background: whitesmoke;

}

.cell_inner {
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    top: 10px;
    left: 10px;
    bottom: 10px;
    right: 10px;
    background: #9A8297;
    border-radius: 8px;
    font-size: 7rem;
    cursor: pointer;
}

.cell_inner:hover {

    background: #D0782A;
}


